import UIComponent from "../../common/UIComponent";
import "./index.css";
export default class PlayBtn extends UIComponent {

  constructor(elRoot, interfaces) {
    super();
    this.elRoot = elRoot;
    this.interfaces = interfaces;
    // this.isLive = interfaces.isLive;
    this.isplay = false;
    this.init();
    // v2.3.10 PLAY_IN 与内核事件名统一
    this.interfaces.on(VhallPlayer.PLAY_IN, res => {
      this.isplay = true;
      this.playBtn.setAttribute("class", "vhallPlayer-playBtn pause");
    });

    this.interfaces.on(VhallPlayer.PAUSE, res => {
      this.isplay = false;
      this.playBtn.setAttribute("class", "vhallPlayer-playBtn play");
    });

  }

  init() {
    this.playBtn = document.createElement("span");
    this.playBtn.setAttribute("class", "vhallPlayer-playBtn play");
    this.playBtn.onclick = this.handlPlayBtnClick.bind(this);
    this.elRoot.appendChild(this.playBtn);
  }

  handlPlayBtnClick() {
    !this.isplay ? this.interfaces.play() : this.interfaces.pause();
  }

}
