/**
 * 所有内置插件的集合
 * deprecated => thorn、Barrage
 */
import Capture from './Capture';
import Fullscreen from './Fullscreen';
import Lag from './Lag';
import Marquee from './Marquee';
import PlaybackRate from './PlaybackRate';
import Reporter from './Reporter';
import Scheduler from './Scheduler';
import Switcher from './Switcher';
import Watermark from './Watermark';
import Pursue from './Pursue';
import PlayCurrentTime from './PlayCurrentTime';
import LiveTimeshift from './LiveTimeshift';
import Subtitle from './Subtitle';
import LiveSubtitle from './LiveSubtitle';
import Poster from './poster';
import Danmaku from './Danmaku';

let PluginMap = new Map();

PluginMap.set(Fullscreen.type, Fullscreen);
PluginMap.set(Capture.type, Capture);
PluginMap.set(PlaybackRate.type, PlaybackRate);
PluginMap.set(Danmaku.type, Danmaku);

if (process.env.PF === 'vhall') {
  PluginMap.set(Scheduler.type, Scheduler);
  PluginMap.set(Switcher.type, Switcher);
  PluginMap.set(Poster.type, Poster);
  PluginMap.set(Lag.type, Lag);
  PluginMap.set(Reporter.type, Reporter);
  PluginMap.set(Marquee.type, Marquee);
  PluginMap.set(Watermark.type, Watermark);
  PluginMap.set(Pursue.type, Pursue);
  PluginMap.set(PlayCurrentTime.type, PlayCurrentTime);
  PluginMap.set(LiveTimeshift.type, LiveTimeshift);
  PluginMap.set(Subtitle.type, Subtitle);
  PluginMap.set(LiveSubtitle.type, LiveSubtitle);
}

export default PluginMap;
